import { html } from 'lit';
import './nav.css';



export const Nav = ({ label, solveable }) => {
    const e = html`<mp-nav title="${label}" is-solveable="${solveable}"></mp-nav>`;
    this.parameters = {docs: { source: { code: `<mp-nav title="${label}" is-solveable="${solveable}"></mp-nav>` } }};
    return e
};


